import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Message, MenuItem, SelectItem } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { BusinessTemplateCreateBean } from './bean/business-template-create.bean';
import * as _ from 'lodash';
import { BusinessCenterManageHttp } from '../../http/business-center-manage.http.service';
// import { BusinessTemplateHttpService } from '../../http/business-template.http.service';

@Component({
	selector: 'business-template-create',
	templateUrl: './business-template-create.component.html',
	styleUrls: ['./business-template-create.component.scss'],
	providers: []
})
//文件模板新增
export class BusinessTemplateCreateComponent implements OnInit {

	dtPermission: any;
	businessTemplateCate: any;
	dtEntity: any;
	mesg: any;//报文头信息
	msgs: Message[] = [];//信息提示框
	tabMesg: string = tableMessage;//表格没有数据时的展示内容
	businessTemplateCreateBean: BusinessTemplateCreateBean = new BusinessTemplateCreateBean();//文件模板信息
	fileType: any;//文件类型
	items: MenuItem[];//下一步
	activeIndex: number;
	vlaue: any;//显示哪个div
	flag: number;//判断重置
	Flag: boolean = false;//判断重置
	code: any;//码值
	oneChooseList: any = [];//单选-数组
	allChooseList: boolean = false;//全选
	detailDisplay: boolean = false;//规则详情展示
	detail: any = {};//详情展示
	custChooseDisplay: boolean = false;
	list: any = [];//假数据
	arr: any = [];//假数据
	tableList1: any = [];//假数据-左边表格
	tableList2: any = [];//假数据-右边表格
	FileTemplateCate: any = [];//模板大类
	flagUpdate: string;
	dtId: string;//模板编号
	inValue: any;
	inValue2: any;
	FiledType: any = [];
	Judge: any = [];
	drName: string;
	drEntityField: string;
	checkBox: any = [];
	checkBox1: any = [];
	chooseData: any = [];
	chooseData1: any = [];
	aa: any;
	DaQueryRule:any;//查询规则
	constructor(
		public commfunc: Commfunc,
		public router: Router,
		public activatedRoute: ActivatedRoute,
		public httpService: BusinessCenterManageHttp
	) {
		this.mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		this.codeValues() //调用方法，获取全部码值
		this.businessTemplateCate = this.code['businessTemplateCate'];//模板大类
		this.FiledType = this.code['FiledType'];//字段类型
		this.Judge = this.code['JUDGE'];//判断是否
		this.dtEntity=this.code['dtEntity'];//实体
		this.dtPermission=this.code['dtPermission'];
		this.DaQueryRule=this.code['DaQueryRule'];
		activatedRoute.queryParams.subscribe(queryParams => {
			this.flagUpdate = queryParams.flag;
		})
		if (this.flagUpdate) {
			this.dtId = this.activatedRoute.snapshot.params['dtId']
			//console.log(this.activatedRoute.snapshot.params);
			this.businessTemplateCreateBean.dtType=this.activatedRoute.snapshot.params.dtType;
			this.businessTemplateCreateBean.dtFiletype=this.activatedRoute.snapshot.params.dtFiletype;
			this.businessTemplateCreateBean.dtPermission=this.activatedRoute.snapshot.params.dtPermission;
			this.businessTemplateCreateBean.dtName=this.activatedRoute.snapshot.params.dtName;
			this.businessTemplateCreateBean.fileTemplateCate=this.activatedRoute.snapshot.params.fileTemplateCate;
			this.businessTemplateCreateBean.entityName=this.activatedRoute.snapshot.params.entityName;
			//console.log(this.activatedRoute.snapshot.params)
		}
	
	}

	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}

	ngOnInit() {
		this.activeIndex = 0;
		this.vlaue = '0';
		this.flag = 0;
		this.items = [
			{
				label: '业务模板基本信息配置',
				command: (event: any) => {
					this.activeIndex = 0;
				}
			},
			{
				label: '模板配置',
				command: (event: any) => {
					this.activeIndex = 1;
				}
			}

		];
		// if (this.flagUpdate == '1') {
		// 	this.updateDetail();
		// }
		this.businessTemplateCreateBean.dtFiletype = '.xlsx';//单选框初始化-excel
		this.businessTemplateCreateBean.dtType = '03';//文件类型
	}

	//添加模板项
	addTemplate() {
		this.custChooseDisplay = true;
		this.inValue = this.dtId;
		this.inValue2 = this.businessTemplateCreateBean.entityName;
	}

	//删除
	delete(col, i) {
		// //console.log(col);
		this.list.splice(this.list[i], 1);
		this.list = _.clone(this.list);
	}

	toRuleInfo(col) {
		this.detailDisplay = true;
		this.detail = col;
	}



	//重置
	reset() {
		if (this.activeIndex == 0) {
			this.businessTemplateCreateBean.dtName = '';
			this.businessTemplateCreateBean.fileTemplateCate = '';
			this.businessTemplateCreateBean.dtDescription = '';
			this.businessTemplateCreateBean.entityName = '';
		} else if (this.activeIndex == 1) {
			this.list.forEach(element => {
				element.daWilllose = '';
				element.daUpdate = '';
				element.daQuery = '';
				element.daAnotherField = '';
				element.daEntityField = '';
				element.daFieldMapping = '';
				element.daDescription = '';
			});
		} 
	}

	//返回
	returnBtn() {
		setTimeout(() => {
			window.history.go(-1);
		}, 500)
	}

	//上一步
	prev() {
		this.activeIndex = this.activeIndex - 1;
		this.vlaue = this.activeIndex;
	}

	//下一步
	next() {
		if (this.flagUpdate) {
			if (!this.businessTemplateCreateBean.dtName) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '模板名称不能为空' });
				return;
			}
	
			if (!this.businessTemplateCreateBean.entityName) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '实体文件名不能为空' });
				return;
			}
			this.businessTemplateCreateBean.dtId=this.dtId;
			this.businessTemplateCreateBean.doTemplateFlag='1';
			this.httpService.modifyDocumentTemplate(this.businessTemplateCreateBean).subscribe(data => {
				if (data.returnCode.code == 'success') {
					this.activeIndex = this.activeIndex + 1;
					this.vlaue = this.activeIndex;
					this.echo();
					this.msgs = [];
					this.msgs.push({ severity: 'success', summary: '提示', detail: "修改成功!" });
				} else {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
				}
			}, error => {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
			})

		} else {
			if (!this.businessTemplateCreateBean.dtName) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '模板名称不能为空' });
				return;
			}
			
			if (!this.businessTemplateCreateBean.entityName) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '实体文件名不能为空' });
				return;
			}
			delete this.businessTemplateCreateBean.doTemplateFlag;
			this.httpService.createTemplate(this.businessTemplateCreateBean).subscribe(data => {
				if (data.returnCode.code == 'success') {
					this.dtId = data.dtId;
					this.activeIndex = this.activeIndex + 1;
					this.vlaue = this.activeIndex;
					setTimeout(()=>{
						this.list=[];
					})
					// this.temPlateClick();
					this.msgs = [];
					this.msgs.push({ severity: 'success', summary: '提示', detail: "创建成功!" });
				} else {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
				}
			}, error => {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
			})
		}

	}
	//修改查询
	echo() {
		let param = {
			dtId: this.dtId,
		}
		this.httpService.queryDocumentTemplateDetails(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.list = data.result.tableList;
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: "查询成功!" });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}
	//模板配置列表删除
	toDelete(i) {
		this.list.splice(i, 1);
		this.list = _.clone(this.list);
	}
	
	//保存
	save() {
		this.list.forEach((item,index)=>{
			item['daNo']=index;
			item['daAnotherField']=item['daField'];
		})
		let param = {
			dtId: this.dtId,
			templateDetailList:this.list
		}
		this.httpService.modifyDocumentTemplateDetails(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: "保存成功!" });
				window.history.go(-1)
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}

	
	//字段选择
	outValue(event) {
		if (event == false) {
			this.custChooseDisplay = event;
		} else {
			event.forEach(element => {
				element['daType'] = element.filedType;
				element['daLength'] = element.filedLen;
			});
			this.list.push(...event);
			let result = [];
			let obj = {};
			for (let i = 0; i < this.list.length; i++) {
				if (!obj[this.list[i].daField]) {
					result.push(this.list[i]);
					obj[this.list[i].daField] = true;
				}
			}
			this.list = _.clone(result);
			this.custChooseDisplay = false;
		}
	}

	getIndex(list, item) {
		let index = -1;
		for (let i = 0; i < list.length; i++) {
			if (list[i].daField == item.daField) {
				index = i;
				break;
			}
		}
		return index
	}
	//上
	up(car) {
		let carList = JSON.parse(JSON.stringify(this.list));
		let i = this.getIndex(carList, car);
		if (i != -1 && i != 0) {
			let a = carList[i - 1];
			carList[i] = a;
			carList[i - 1] = car;
		}
		this.list = carList;
	}

	//下
	down(car) {
		let carList = JSON.parse(JSON.stringify(this.list));
		let i = this.getIndex(carList, car);
		if (i < carList.length - 1) {
			let a = carList[i + 1];
			carList[i] = a;
			carList[i + 1] = car;
		}
		this.list = carList;
	}

	//修改调详情接口
	updateDetail() {
		this.httpService.findDocumentTemplateDetails({ dtId: this.dtId }).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.businessTemplateCreateBean = data.docuTem;
				this.list = data.docuTem.attrs;
				//console.log(this.list)
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: "修改成功!" });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}

}