import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { BusinessTemplateComponent } from './business-template';
import { BusinessTemplateCreateComponent } from './business-template-create/business-template-create.component';
import { BusinessTemplateDetailComponent } from './business-template-detail/file-template-detail.component';


const routes: Routes = [
    { path: '', component: BusinessTemplateComponent },
    { path: 'business-template-create', component: BusinessTemplateCreateComponent },
    { path: 'business-template-detail', component: BusinessTemplateDetailComponent }

]

export const BusinessTemplateRouting: ModuleWithProviders = RouterModule.forChild(routes);