import { Component, OnInit } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
// import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { Message, ConfirmationService } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { TZB_COMMON_DOWNLOAD, TZB_CUS_UPLOAD } from 'app/@uisftech/common/service/http.constant';
import { BusinessTemplateManageBean } from './bean/business-template-manage.bean';
import { BusinessCenterManageHttp } from '../http/business-center-manage.http.service';

@Component({
    selector: 'business-template',
    templateUrl: './business-template.html',
    styleUrls: ['./business-template.scss'],
    providers: []
})

export class BusinessTemplateComponent implements OnInit {
    msgs: Message[] = [];//信息提示框
    flag: boolean = true;//判断是否是初始化
    businessTemplateManageBean: BusinessTemplateManageBean = new BusinessTemplateManageBean();//文件模板信息
    tabMesg: string = tableMessage;//表格没有数据时的展示内容
    list: any = [];//假数据
    code: any;//获取所有码值
    total: number;
    first: number = 0;//首页
    businessTemplateCate: any = [];
    DtType: any = [];
    DOWNLOAD: string = TZB_COMMON_DOWNLOAD;
    UPLOAD: string = TZB_CUS_UPLOAD;
    FiledStatus: any = [];
    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public httpService: BusinessCenterManageHttp,
        public confirmationService: ConfirmationService,
    ) {
        this.codeValues();
        this.businessTemplateCate = this.code['businessTemplateCate'];//模板大类
        this.FiledStatus = this.code['FiledStatus'];//状态
        this.DtType = this.code['DTTYPE'];//模板类型
    }

    ngOnInit() {
        this.businessTemplateManageBean.pageSize = 10;
        this.businessTemplateManageBean.pageNum = 1;
        this.queryClick();
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //查询
    query() {
        this.businessTemplateManageBean.pageSize = 10;
        this.businessTemplateManageBean.pageNum = 1;
        this.queryClick();
    }

    queryClick() {
        this.httpService.queryDocumentTemplates(this.businessTemplateManageBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.total = data.result.total;
                this.list = data.result.templateInfoList ? data.result.templateInfoList : [];
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //分页
    paginate(event) {
        //每页显示的条数
        this.businessTemplateManageBean.pageSize = Number(event.rows);
        //当前页
        let currentPage = event.page + 1;
        this.businessTemplateManageBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.businessTemplateManageBean.pageSize;
        this.queryClick();//调用查询的方法
    }
    //新增模板
    addTemplate() {
        this.router.navigate(['/pages/tzb/back-manage/base-manage/business-center-manage/business-template/business-template-create']);
    }

    //查看
    view(item) {
        this.router.navigate(['/pages/tzb/back-manage/base-manage/business-center-manage/business-template/business-template-detail', item]);
    }

    //下载
    download(item) {
        let param = {
            dtId: item.dtId,
            outCount: '1'
        }
        this.httpService.downLodeDocumentTemplate(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                window.open(this.DOWNLOAD + "?fileName=" + encodeURI(data.result.fileName) + "&fileUrl=" + data.result.fileUrl, "_self")
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        })
    }

    //修改
    update(item) {
        this.router.navigate(['/pages/tzb/back-manage/base-manage/business-center-manage/business-template/business-template-create', item], { queryParams: { flag: '1' } });
    }

    //删除
    delete(item) {
        //删除
        this.confirmationService.confirm({
            message: '确定要删除吗?',
            accept: () => {
                let param = {
                    dtId: item.dtId,
                    dtStatusId: '1',
                    doTemplateFlag: '0'
                }
                this.httpService.modifyDocumentTemplate(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: "删除成功!" });
                        this.queryClick();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务错误' });
                })
            }
        });
    }

    //键盘事件
    enter(event) {
        let e = event || window.event
        if (e && e.keyCode == 13) {
            this.queryClick();
        }
    }
}