import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Message, MenuItem, SelectItem } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import * as _ from 'lodash';
import { BusinessTemplateManageBean } from '../bean/business-template-manage.bean';
import { BusinessCenterManageHttp } from '../../http/business-center-manage.http.service';

@Component({
	selector: 'file-template-detail',
	templateUrl: './file-template-detail.component.html',
	styleUrls: ['./file-template-detail.component.scss'],
	providers: []
})
/**
 * 文件模板详情
 */
export class BusinessTemplateDetailComponent implements OnInit {

	tableList: any[];
	entityName: any;
	mesg: any;//报文头信息
	msgs: Message[] = [];//信息提示框
	tabMesg: string = tableMessage;//表格没有数据时的展示内容
	businessTemplateManageBean: BusinessTemplateManageBean = new BusinessTemplateManageBean();//文件模板信息
	fileType: any;//文件类型
	items: MenuItem[];//下一步
	activeIndex: number;
	vlaue: any;//显示哪个div
	flag: number;//判断重置
	Flag: boolean = false;//判断重置
	code: any;//码值
	oneChooseList: any = [];//单选-数组
	allChooseList: boolean = false;//全选
	detailDisplay: boolean = false;//规则详情展示
	detail: any = {};//详情展示
	arr: any = [];//假数据
	tableList1: any = [];//假数据-左边表格
	tableList2: any = [];//假数据-右边表格
	aaa: any;
	bbb: SelectItem[];
	ccc: any = [];
	dtId: string;//模板id
	dataMap: any = {};
	dataList1: any = [];
	dataList2: any = [];
	dataList3: any = [];
	Judge: any = [];//判断是否
	businessTemplateCate: any = [];
	DtType: any = [];
	FiledType: any = [];
	first: number = 0;
	constructor(
		public commfunc: Commfunc,
		public router: Router,
		public httpService: BusinessCenterManageHttp,
		public activatedRoute: ActivatedRoute
	) {
		this.mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		this.codeValues() //调用方法，获取全部码值
		this.Judge = this.code['JUDGE'];//判断是否
		this.businessTemplateCate = this.code['businessTemplateCate'];//模板大类
		this.DtType = this.code['DTTYPE'];//模板类型
		this.FiledType = this.code['FiledType'];//字段类型
		this.businessTemplateManageBean.dtId = this.activatedRoute.snapshot.params['dtId'];
		this.entityName = this.activatedRoute.snapshot.params['entityName'];
		this.echo();
	}

	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}

	ngOnInit() {
		this.businessTemplateManageBean.pageSize = 10;
		this.businessTemplateManageBean.pageNum = 1;
	}


	//详情
	echo() {
		let param = {
			dtId: this.businessTemplateManageBean.dtId,
		}
		this.httpService.queryDocumentTemplateDetails(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.dataMap = data.result.templateInfo;
				this.tableList = data.result.tableList;
				this.query();
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: "查询成功!" });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}

	query() {
		let param = {
			dtId: this.businessTemplateManageBean.dtId,
			pageSize: this.businessTemplateManageBean.pageSize,
			pageNum: this.businessTemplateManageBean.pageNum,
		}
		this.httpService.queryPermissionInfoList(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: '查询成功' });
				this.businessTemplateManageBean.total = data.result.total;
				if (data.result.permissionsInfoList) {
					this.dataList1 = data.result.permissionsInfoList;
				} else {
					this.dataList1 = [];
				}
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '查询失败' })
			}
		})
	}

	//返回
	returnBtn() {
		setTimeout(() => {
			window.history.go(-1);
		}, 500)
	}

	//分页
    paginate(event) {
        this.businessTemplateManageBean.pageSize = Number(event.rows);//每页显示的条数
        let currentPage = event.page + 1;//当前页
        this.businessTemplateManageBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.businessTemplateManageBean.pageSize;
        this.query();//调用查询的方法
    }

}